import React from 'react';
import { TEXT } from './config';
import Modal from '../Modal/Modal';

class Input extends React.Component {
    render(){
        return (
            <section className="input">
                <Modal position={'first_page'} text={TEXT.TOOLTIP}/>
                <p className="input--title">{TEXT.INPUT_TITLE}</p>
                <input className="input--field" pattern="[0-9]{5}" type="text"/>
            </section>
        );
    }
}

export default Input;



