import React from 'react';
import { TEXT } from './config';
import { APIConsumerCount } from '../../API/index';

class Header extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            plans: []
        };
    }

    componentDidMount() {
        APIConsumerCount('eyJ6aXAiOiAiMzMxNzgifQ')
        .then(res => {
            const plans = res.data.count;
            this.setState({plans});
        })
        .catch(error => error);
    }

    render() {
        return (
            <section className="header-container section">
                <header className="header-container-wrapper section--full-bleed">
                    <h1 className="header-container-wrapper--title">{TEXT.HEADER_TITLE}</h1>
                    <p className="header-container--paragraph">{this.state.plans} {TEXT.HEADER_PLANS}</p>
                </header>
            </section>
        );
    }
}

export default Header;



