import React from 'react';
import { Header, Slider, Input, Dropdown, Button, Link } from '../Components/index';
import { TEXT } from './config';


class App extends React.Component {
  render() {
    return (
      <React.Fragment>
        <Header />
        <Slider />
        <Input />
        <Dropdown />
        <Button title={TEXT} />
        <Link />
      </React.Fragment>
    );
  }
}

export default App;



