import React from 'react';
// import PropTypes from 'prop-types';
import Slider from 'rc-slider';
import { MARKS, TEXT } from './config';

class MySlider extends React.Component {

    render() {
        function log(value) {
            console.log(value);
        }

        return (
        <section className ="slider-container section">
            <div className= "section--wrap">
                <p className="slider-container--paragraph">{TEXT.SLIDER_INFORMATION}</p>
                <h2 className="slider-container--title grid__row">{TEXT.SLIDER_TITLE}</h2>
                <div className="slider-container--wrapper">
                    <Slider
                        min={0}
                        max={4}
                        marks={MARKS}
                        step={1}
                        onChange={log}
                        defaultValue={0}/>
                </div>
            </div>
        </section>
        );
    }
}

export default MySlider;



